/**
 * FoilsTable Component
 * 
 * IMPORTANT: Column Alignment
 * - The table uses fixed column widths for consistent layout and horizontal scrolling
 * - Each column width in headerCell must match the corresponding cell width
 * - Current widths:
 *   - Regular columns (brand, model, type, series): 150px
 *   - Number columns (area, wingspan, AR, price): 100px
 *   - Link column: 100px
 * - When modifying column styles, always update both headerCell and cell styles to maintain alignment
 * - Table has a minWidth of 1200px to ensure proper layout on all screen sizes
 */

import React, { useEffect } from 'react';
import { View, Text, Pressable, ScrollView, StyleSheet, Platform, useWindowDimensions } from 'react-native';
import { BaseProduct, SortField } from '../types/scraper';
import { useTheme } from '../theme/ThemeContext';
import { CONTAINER_PADDING, MOBILE_BREAKPOINT, COLUMN_WIDTHS, getTableWidth, getContentWidth, Column } from '../constants/layout';
import { saveSortOption, loadSortOption } from '../utils/storage';
import { trackAction, ANALYTICS_EVENTS } from '../utils/analytics';
import { spacing } from '../theme/spacing';

interface FilterOptions {
  brands: string[];
  series: string[];
  type: string[];
}

interface FoilsTableProps {
  foils: BaseProduct[];
  onSort: (field: SortField) => void;
  onFoilSelect: (foil: BaseProduct) => void;
  isSelected: (foil: BaseProduct) => boolean;
  isTemporary?: (foil: BaseProduct) => boolean;
  sortOption: { field: SortField; ascending: boolean };
  columns: Column[];
  filters: FilterOptions;
  selectedFoils?: BaseProduct[];
  onCompare?: () => void;
  onClearSelection?: () => void;
}

const DEFAULT_COLUMNS = [
  { id: 'brand', label: 'Brand', isVisible: true },
  { id: 'series', label: 'Series', isVisible: false },
  { id: 'model', label: 'Model', isVisible: true },
  { id: 'area', label: 'Area', isVisible: true },
  { id: 'wingspan', label: 'Span', isVisible: true },
  { id: 'aspectRatio', label: 'AR', isVisible: true },
  { id: 'type', label: 'Type', isVisible: true },
  { id: 'price', label: 'Price', isVisible: true },
  { id: 'link', label: 'Link', isVisible: true },
];

export const FoilsTable: React.FC<FoilsTableProps> = ({
  foils,
  onSort,
  onFoilSelect,
  isSelected,
  isTemporary,
  sortOption,
  columns,
  filters,
  selectedFoils,
  onCompare,
  onClearSelection,
}) => {
  const { width } = useWindowDimensions();
  const isMobile = width < MOBILE_BREAKPOINT;
  const { colors } = useTheme();

  // Load saved sort option on mount
  useEffect(() => {
    const loadSavedSort = async () => {
      try {
        const savedSort = await loadSortOption();
        if (savedSort && (savedSort.field !== sortOption.field || savedSort.ascending !== sortOption.ascending)) {
          onSort(savedSort.field);
        }
      } catch (error) {
        console.error('Error loading sort option:', error);
      }
    };

    loadSavedSort();
  }, []);

  // Save sort option when it changes
  useEffect(() => {
    const saveSortSettings = async () => {
      try {
        await saveSortOption({
          field: sortOption.field,
          ascending: sortOption.ascending
        });
      } catch (error) {
        console.error('Error saving sort option:', error);
      }
    };

    saveSortSettings();
  }, [sortOption]);

  const getColumnWidth = (columnId: string) => {
    return COLUMN_WIDTHS[columnId as keyof typeof COLUMN_WIDTHS] || 150;
  };

  const isColumnFiltered = (columnId: string) => {
    switch (columnId) {
      case 'brand':
        return filters.brands.length > 0;
      case 'series':
        return filters.series.length > 0;
      case 'type':
        return filters.type.length > 0;
      default:
        return false;
    }
  };

  const renderSortIcon = (columnId: string) => {
    if (sortOption.field !== columnId) {
      return (
        <View style={styles.sortIconContainer}>
          <Text style={[styles.sortIcon, { color: colors.text.tertiary }]}>▲</Text>
          <Text style={[styles.sortIcon, { color: colors.text.tertiary }]}>▼</Text>
        </View>
      );
    }

    return (
      <View style={styles.sortIconContainer}>
        <Text style={[
          styles.sortIcon,
          sortOption.ascending && [styles.sortIconUpActive, { color: colors.primary }]
        ]}>▲</Text>
        <Text style={[
          styles.sortIcon,
          !sortOption.ascending && [styles.sortIconDownActive, { color: colors.primary }]
        ]}>▼</Text>
      </View>
    );
  };

  const getCellContent = (foil: BaseProduct, columnId: string) => {
    let content: React.ReactNode = '-';
    
    switch (columnId) {
      case 'brand':
        content = foil.brand;
        break;
      case 'model':
        content = foil.model;
        break;
      case 'series':
        content = foil.series || '-';
        break;
      case 'area':
        content = foil.specs?.area?.toFixed(2) || '-';
        break;
      case 'wingspan':
        content = foil.specs?.wingspan?.toFixed(2) || '-';
        break;
      case 'aspectRatio':
        content = foil.specs?.aspectRatio?.toFixed(2) || '-';
        break;
      case 'type':
        content = foil.type || '-';
        break;
      case 'price':
        content = foil.price ? `$${foil.price}` : '-';
        break;
      case 'link':
        return (
          <Pressable
            style={[styles.linkButton, { backgroundColor: colors.primary }]}
            onPress={() => window.open(foil.sourceUrl, '_blank')}
          >
            <Text style={[styles.linkText, { color: colors.text.inverse }]}>View</Text>
            <Text style={[styles.externalLinkIcon, { color: colors.text.inverse }]}>↗</Text>
          </Pressable>
        );
    }

    return (
      <Text 
        style={[
          styles.cellText,
          { color: colors.text.primary },
          ['area', 'wingspan', 'aspectRatio', 'price'].includes(columnId) && styles.numberText
        ]}
      >
        {content}
      </Text>
    );
  };

  const handleSort = (field: SortField) => {
    trackAction(ANALYTICS_EVENTS.SORT_FOILS, {
      field,
      direction: sortOption.field === field && sortOption.ascending ? 'desc' : 'asc'
    });
    onSort(field);
  };

  return (
    <View style={[styles.container, { backgroundColor: colors.background.primary }]}>
      <ScrollView 
        horizontal 
        showsHorizontalScrollIndicator={true}
        style={[
          styles.horizontalScrollView,
          isMobile && {
            marginHorizontal: -CONTAINER_PADDING,
            paddingHorizontal: CONTAINER_PADDING
          }
        ]}
      >
        <View style={styles.tableContent}>
          <View style={[
            styles.tableHeader,
            { backgroundColor: colors.background.tertiary }
          ]}>
            <View style={[
              styles.headerRow,
              { 
                borderBottomColor: colors.border,
                backgroundColor: colors.background.secondary,
              }
            ]}>
              {columns.map(column => column.isVisible && (
                <Pressable
                  key={column.id}
                  style={[
                    styles.headerCell,
                    { 
                      width: getColumnWidth(column.id),
                      borderRightColor: colors.text.tertiary + '40',
                      borderBottomColor: isColumnFiltered(column.id) ? colors.primary : colors.border,
                      backgroundColor: colors.background.tertiary,
                      borderRightWidth: 1,
                    },
                    isColumnFiltered(column.id) && styles.headerCellFiltered
                  ]}
                  onPress={() => handleSort(column.id as SortField)}
                >
                  <Text style={[
                    styles.headerText,
                    { 
                      color: isColumnFiltered(column.id) ? colors.primary : colors.text.primary,
                      fontWeight: '600',
                    }
                  ]}>
                    {column.label}
                  </Text>
                  {renderSortIcon(column.id)}
                </Pressable>
              ))}
            </View>
          </View>
          <ScrollView 
            style={[styles.scrollView, { backgroundColor: colors.background.primary }]}
            showsVerticalScrollIndicator={true}
            bounces={false}
          >
            {foils.map((foil, index) => {
              const selected = isSelected(foil);
              const temporary = isTemporary?.(foil);
              const rowKey = `${foil.model}-${foil.specs?.area || 'unknown'}-${index}`;
              
              return (
                <Pressable
                  key={rowKey}
                  onPress={() => onFoilSelect(foil)}
                  style={({ pressed }) => [
                    styles.row,
                    { borderBottomColor: colors.border },
                    index % 2 === 0 && [styles.rowEven, { backgroundColor: colors.background.secondary + '50' }],
                    selected && [styles.rowSelected, { 
                      backgroundColor: `${colors.primary}15`,
                    }],
                    temporary && [styles.rowTemporary, {
                      backgroundColor: `${colors.success}10`,
                    }],
                    Platform.OS === 'web' && pressed && !selected && [styles.rowHovered, { 
                      backgroundColor: `${colors.primary}10`,
                    }]
                  ]}
                >
                  {columns.map(column => column.isVisible && (
                    <View
                      key={`${rowKey}-${column.id}`}
                      style={[
                        styles.cell,
                        { 
                          width: getColumnWidth(column.id),
                          borderRightColor: colors.border,
                          borderRightWidth: 1,
                        }
                      ]}
                    >
                      {getCellContent(foil, column.id)}
                    </View>
                  ))}
                </Pressable>
              );
            })}
          </ScrollView>
        </View>
      </ScrollView>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    width: '100%',
  },
  horizontalScrollView: {
    flex: 1,
    width: '100%',
  },
  tableContent: {
    flex: 1,
    minWidth: '100%',
  },
  tableHeader: {
    borderBottomWidth: 1,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.05,
    shadowRadius: 1,
    elevation: 2,
  },
  headerRow: {
    flexDirection: 'row',
    borderBottomWidth: 1,
  },
  headerCell: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: spacing.MEDIUM,
    borderRightWidth: 1,
    borderBottomWidth: 2,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
      transition: 'all 0.2s ease',
    }),
  },
  headerCellFiltered: {
    borderBottomWidth: 2,
  },
  headerText: {
    fontSize: 14,
    flex: 1,
    letterSpacing: 0.3,
  },
  row: {
    flexDirection: 'row',
    borderBottomWidth: 1,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
    }),
  },
  rowEven: {},
  rowSelected: {
    borderStyle: 'solid',
  },
  rowHovered: {
    borderStyle: 'solid',
  },
  cell: {
    padding: spacing.MEDIUM,
    borderRightWidth: 1,
  },
  cellText: {
    fontSize: 14,
  },
  numberText: {
    textAlign: 'right',
  },
  scrollView: {
    flex: 1,
  },
  sortIconContainer: {
    flexDirection: 'column',
    alignItems: 'center',
    height: 28,
    justifyContent: 'center',
    width: 16,
    gap: spacing.XSMALL,
    marginLeft: spacing.SMALL,
  },
  sortIcon: {
    fontSize: 12,
    width: 12,
    height: 10,
    lineHeight: 10,
    textAlign: 'center',
  },
  sortIconUpActive: {
    fontSize: 13,
  },
  sortIconDownActive: {
    fontSize: 13,
  },
  linkButton: {
    paddingHorizontal: 8,
    paddingVertical: 4,
    borderRadius: 4,
    minWidth: 44,
    alignItems: 'center',
    position: 'relative',
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
    }),
  },
  linkText: {
    fontSize: 12,
    fontWeight: '600',
  },
  externalLinkIcon: {
    position: 'absolute',
    top: 0,
    right: 2,
    fontSize: 12,
    fontWeight: '600',
  },
  rowTemporary: {
    borderStyle: 'dashed',
  },
});
