import { useState, useMemo } from 'react';
import { BaseProduct, SortField } from '../types/scraper';
import { sortProducts } from '../utils/sorting';

export interface SortOption {
  field: SortField;
  direction: 'asc' | 'desc';
}

export function useSortedFoils(foils: BaseProduct[]) {
  const [sortOption, setSortOption] = useState<SortOption>({
    field: 'area',
    direction: 'desc'
  });

  const sortedFoils = useMemo(() => {
    return sortProducts(foils, sortOption.field, sortOption.direction);
  }, [foils, sortOption]);

  return {
    sortOption,
    setSortOption,
    sortedFoils
  };
} 