import React, { useState } from 'react';
import { View, StyleSheet, Platform, TouchableOpacity, Text } from 'react-native';
import { FoilCollectionView } from './FoilCollectionView';
import ComparisonControls from './ComparisonControls';
import { QuiverManager } from './QuiverManager';
import { BaseProduct } from '../types/scraper';
import { Column } from '../constants/layout';
import { useTheme } from '../theme/ThemeContext';
import { useRoute } from '@react-navigation/native';
import { RootStackParamList } from '../types/navigation';
import { trackAction, ANALYTICS_EVENTS } from '../utils/analytics';
import { spacing } from '../theme/spacing';
import { FilterOptions } from '../types/scraper';

interface ComparisonViewProps {
  foils: BaseProduct[];
  allFoils: BaseProduct[];
  columns: Column[];
  onClose?: () => void;
  quiverName?: string;
  onUpdateQuiver?: (foils: BaseProduct[]) => void;
  searchQuery?: string;
  onSearch?: (text: string) => void;
  onColumnVisibilityChange: (columnId: string) => void;
  onResetColumns: () => void;
  filterOptions: {
    brands: string[];
    series: string[];
    types: string[];
  };
  selectedFilters: FilterOptions;
  onFilterChange: (type: keyof FilterOptions, value: string) => void;
  onClearFilters: () => void;
}

const ComparisonView: React.FC<ComparisonViewProps> = ({ 
  foils: initialFoils, 
  allFoils, 
  onClose, 
  columns,
  quiverName,
  onUpdateQuiver,
  searchQuery = '',
  onSearch,
  onColumnVisibilityChange,
  onResetColumns,
  filterOptions,
  selectedFilters,
  onFilterChange,
  onClearFilters,
}: ComparisonViewProps) => {
  const { colors } = useTheme();
  const route = useRoute<{ key: string; name: keyof RootStackParamList; params: RootStackParamList['Browse'] }>();
  const isQuiverView = route.params?.isQuiverView;
  const [isQuiverManagerVisible, setIsQuiverManagerVisible] = useState(false);
  const [foils, setFoils] = useState<BaseProduct[]>(initialFoils);

  const handleUpdateFoils = (updatedFoils: BaseProduct[]) => {
    setFoils(updatedFoils);
    if (isQuiverView) {
      onUpdateQuiver?.(updatedFoils);
      trackAction(ANALYTICS_EVENTS.UPDATE_QUIVER, {
        quiverName,
        foilCount: updatedFoils.length
      });
    }
  };

  const renderControls = () => (
    <View style={styles.controlsContainer}>
      <View style={styles.controlsInnerContainer}>
        <View style={styles.controlsColumn}>
          {!isQuiverView && (
            <TouchableOpacity
              style={[styles.button, { backgroundColor: colors.primary }]}
              onPress={() => setIsQuiverManagerVisible(true)}
            >
              <Text style={[styles.buttonText, { color: colors.text.inverse }]}>
                Save
              </Text>
            </TouchableOpacity>
          )}
          <ComparisonControls
            selectedFoils={foils}
            onCompareSelected={() => {}}
            onExitCompare={onClose || (() => {})}
            onClearSelection={() => {}}
            isComparisonView={true}
            isQuiverView={isQuiverView}
          />
        </View>
      </View>
    </View>
  );

  return (
    <View style={styles.container}>
      <FoilCollectionView
        foils={foils}
        allFoils={allFoils}
        columns={columns}
        searchQuery={searchQuery}
        onSearch={onSearch}
        onUpdateFoils={handleUpdateFoils}
        renderControls={renderControls}
      />

      <QuiverManager 
        selectedFoils={foils} 
        onClose={() => setIsQuiverManagerVisible(false)}
        initialTab={isQuiverView ? 'saved' : 'save'}
        isModalVisible={isQuiverManagerVisible}
        hideStandaloneButton={true}
      />
    </View>
  );
};

export default ComparisonView;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    position: 'relative',
  },
  controlsContainer: {
    position: 'absolute',
    bottom: spacing.CONTROLS_BOTTOM,
    right: 0,
    left: 0,
    paddingHorizontal: spacing.CONTAINER_PADDING,
    zIndex: 1000,
    ...(Platform.OS === 'web' && {
      pointerEvents: 'none',
    }),
  },
  controlsInnerContainer: {
    alignItems: 'flex-end',
  },
  controlsColumn: {
    alignItems: 'flex-end',
    gap: spacing.BUTTON_GAP,
    ...(Platform.OS === 'web' && {
      pointerEvents: 'auto',
    }),
  },
  button: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    paddingHorizontal: spacing.BUTTON_PADDING_H,
    paddingVertical: spacing.BUTTON_PADDING_V,
    borderRadius: 8,
    gap: spacing.SMALL,
    minHeight: 32,
    minWidth: 90,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
      pointerEvents: 'auto',
      userSelect: 'none',
      boxShadow: '0px 2px 4px rgba(0,0,0,0.1)',
    }),
  },
  buttonText: {
    fontSize: 14,
    fontWeight: '600',
    textAlign: 'center',
    ...(Platform.OS === 'web' && {
      userSelect: 'none',
    }),
  },
});