import React, { useState, useCallback } from 'react';
import { View, StyleSheet, Alert, Platform } from 'react-native';
import { FoilsTable } from './FoilsTable';
import { Toast } from './Toast';
import { useTheme } from '../theme/ThemeContext';
import { spacing } from '../theme/spacing';
import { BaseProduct, FilterOptions } from '../types/scraper';
import { Column } from '../constants/layout';
import { useSearchFoils } from '../hooks/useSearchFoils';
import { useRoute } from '@react-navigation/native';
import { RootStackParamList } from '../types/navigation';
import { useSortedFoils } from '../hooks/useSortedFoils';
import { SortField } from '../types/scraper';

interface FoilCollectionViewProps {
  foils: BaseProduct[];
  allFoils: BaseProduct[];
  columns: Column[];
  searchQuery: string;
  onSearch?: (query: string) => void;
  onUpdateFoils?: (foils: BaseProduct[]) => void;
  renderHeader?: () => React.ReactNode;
  renderControls?: () => React.ReactNode;
  onColumnVisibilityChange?: (columnId: string) => void;
  onResetColumns?: () => void;
  filterOptions?: {
    brands: string[];
    series: string[];
    types: ('front' | 'tail')[];
  };
  selectedFilters?: FilterOptions;
  onFilterChange?: (type: keyof FilterOptions, value: string) => void;
  onClearFilters?: () => void;
}

export const FoilCollectionView: React.FC<FoilCollectionViewProps> = ({
  foils,
  allFoils,
  columns,
  searchQuery,
  onSearch,
  onUpdateFoils,
  renderHeader,
  renderControls,
  onColumnVisibilityChange,
  onResetColumns,
  filterOptions,
  selectedFilters,
  onFilterChange,
  onClearFilters,
}) => {
  const { colors } = useTheme();
  const [showToast, setShowToast] = useState(false);
  const [toastMessage, setToastMessage] = useState('');
  const route = useRoute<{ key: string; name: keyof RootStackParamList }>();
  const isComparisonView = route.name === 'Comparison';

  // Use the search hook to filter allFoils based on searchQuery
  const { searchedFoils } = useSearchFoils(allFoils, searchQuery);

  // Combine current foils with search results, marking search results as temporary
  const displayFoils = React.useMemo(() => {
    if (isComparisonView) {
      if (searchQuery) {
        // Get search results that aren't already in the current foils
        const newSearchResults = searchedFoils.filter(searchFoil => 
          !foils.some(existingFoil => 
            existingFoil.model === searchFoil.model && 
            existingFoil.brand === searchFoil.brand
          )
        );
        // Show search results at the top, followed by current foils
        return [...newSearchResults, ...foils];
      }
      // Without search, show current foils
      return foils;
    }

    // For other views, combine current foils with search results
    if (searchQuery) {
      const newSearchResults = searchedFoils.filter(searchFoil => 
        !foils.some(existingFoil => 
          existingFoil.model === searchFoil.model && 
          existingFoil.brand === searchFoil.brand
        )
      );
      return [...foils, ...newSearchResults];
    }

    return foils;
  }, [foils, searchedFoils, searchQuery, isComparisonView]);

  // Add sorting state
  const { sortOption, setSortOption, sortedFoils } = useSortedFoils(displayFoils);

  const handleSort = (field: SortField) => {
    setSortOption(prev => ({
      field,
      direction: prev.field === field && prev.direction === 'asc' ? 'desc' : 'asc'
    }));
  };

  const removeFoil = useCallback((foil: BaseProduct) => {
    const updatedFoils = foils.filter(f => 
      !(f.model === foil.model && f.brand === foil.brand)
    );
    onUpdateFoils?.(updatedFoils);
    setToastMessage(`Removed ${foil.model} from your comparison`);
    setShowToast(true);
    setTimeout(() => setShowToast(false), 2000);
  }, [foils, onUpdateFoils]);

  const handleFoilSelect = useCallback((foil: BaseProduct) => {
    const isExisting = foils.some(f => 
      f.model === foil.model && 
      f.brand === foil.brand
    );

    if (isExisting && isComparisonView) {
      // Show confirmation dialog for removing foils in comparison view
      if (Platform.OS === 'web') {
        const confirmed = window.confirm(`Are you sure you want to remove ${foil.model} from your comparison?`);
        if (confirmed) {
          removeFoil(foil);
        }
      } else {
        Alert.alert(
          'Remove Foil',
          `Are you sure you want to remove ${foil.model} from your comparison?`,
          [
            {
              text: 'Cancel',
              style: 'cancel'
            },
            {
              text: 'Remove',
              style: 'destructive',
              onPress: () => removeFoil(foil)
            }
          ]
        );
      }
    } else {
      // Add new foil or remove in non-comparison view
      if (isExisting) {
        const updatedFoils = foils.filter(f => 
          !(f.model === foil.model && f.brand === foil.brand)
        );
        onUpdateFoils?.(updatedFoils);
        setToastMessage(`Removed ${foil.model} from your collection`);
      } else {
        onUpdateFoils?.([...foils, foil]);
        setToastMessage(`Added ${foil.model} to your collection`);
      }
      setShowToast(true);
      setTimeout(() => setShowToast(false), 2000);
    }
  }, [foils, onUpdateFoils, isComparisonView, removeFoil]);

  const isSelected = useCallback((foil: BaseProduct) => {
    if (isComparisonView) {
      // In comparison view with search, highlight foils that are NOT in the current selection
      if (searchQuery) {
        return !foils.some(f => 
          f.model === foil.model && 
          f.brand === foil.brand
        );
      }
      // Without search, no highlighting
      return false;
    }
    return false;
  }, [foils, isComparisonView, searchQuery]);

  const isTemporary = useCallback((foil: BaseProduct) => {
    return !foils.some(f => 
      f.model === foil.model && 
      f.brand === foil.brand
    );
  }, [foils]);

  return (
    <View style={[styles.container, { backgroundColor: colors.background.primary }]}>
      {renderHeader?.()}
      
      <View style={styles.content}>
        <FoilsTable
          foils={sortedFoils}
          columns={columns}
          sortOption={{
            field: sortOption.field,
            ascending: sortOption.direction === 'asc'
          }}
          onSort={handleSort}
          onFoilSelect={handleFoilSelect}
          isSelected={isSelected}
          isTemporary={isTemporary}
          filters={{ brands: [], series: [], type: [] }}
        />
      </View>

      {renderControls?.()}

      {showToast && (
        <Toast message={toastMessage} type="success" />
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    ...(Platform.OS === 'web' ? {
      maxWidth: '100%',
      width: '100%',
      overflow: 'hidden',
    } : {
      padding: spacing.MEDIUM,
    }),
  },
  content: {
    flex: 1,
    ...(Platform.OS === 'web' ? {
      padding: spacing.MEDIUM,
    } : {}),
  },
}); 