import React from 'react';
import { View, Text, Pressable, StyleSheet, Platform, TouchableOpacity } from 'react-native';
import { BaseProduct } from '../types/scraper';
import { spacing } from '../theme/spacing';
import { colors } from '../theme/colors';
import { useTheme } from '../theme/ThemeContext';
import { useNavigation } from '@react-navigation/native';
import { Button } from './Button';

interface ComparisonControlsProps {
  selectedFoils: BaseProduct[];
  onCompareSelected: () => void;
  onExitCompare: () => void;
  onClearSelection: () => void;
  isComparisonView?: boolean;
  isQuiverView?: boolean;
}

const ComparisonControls = ({ 
  selectedFoils, 
  onCompareSelected, 
  onExitCompare, 
  onClearSelection, 
  isComparisonView = false,
  isQuiverView = false
}: ComparisonControlsProps) => {
  const { colors } = useTheme();
  const navigation = useNavigation();

  // Only show controls if there are selected foils or we're in comparison view
  if (selectedFoils.length === 0 && !isComparisonView) {
    return null;
  }

  const handleCompare = () => {
    if (selectedFoils.length === 0) return;
    
    // Navigate to the dedicated comparison screen
    navigation.navigate('Comparison', {
      foils: selectedFoils,
    });
  };

  return !isComparisonView ? (
    <View style={styles.comparisonBar}>
      <View style={styles.buttonGroup}>
        <Pressable
          style={[styles.button, styles.clearButton]}
          onPress={onClearSelection}
        >
          <Text style={[styles.buttonText, styles.clearButtonText]}>Clear</Text>
        </Pressable>
        <Pressable
          style={[styles.button, styles.compareButton, selectedFoils.length < 2 && styles.buttonDisabled]}
          onPress={handleCompare}
          disabled={selectedFoils.length < 2}
        >
          <Text style={[styles.buttonText, styles.compareButtonText]}>Compare</Text>
        </Pressable>
      </View>
    </View>
  ) : (
    <View style={styles.comparisonBar}>
      <TouchableOpacity
        style={[styles.button, { backgroundColor: colors.background.secondary }]}
        onPress={onExitCompare}
      >
        <Text style={[styles.buttonText, { color: colors.text.primary }]}>
          {isQuiverView ? 'Close Quiver' : 'Back'}
        </Text>
      </TouchableOpacity>
    </View>
  );
};

export default ComparisonControls;

const styles = StyleSheet.create({
  comparisonBar: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'flex-end',
    gap: spacing.MEDIUM,
    backgroundColor: 'transparent',
  },
  buttonGroup: {
    flexDirection: 'row',
    gap: spacing.MEDIUM,
  },
  button: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    paddingHorizontal: spacing.MEDIUM,
    paddingVertical: spacing.SMALL,
    borderRadius: 8,
    gap: 6,
    minHeight: 32,
    minWidth: 90,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
      pointerEvents: 'auto',
      userSelect: 'none',
      boxShadow: '0px 2px 4px rgba(0,0,0,0.1)',
    }),
  },
  buttonDisabled: {
    opacity: 0.5,
  },
  clearButton: {
    backgroundColor: colors.background.primary,
    borderWidth: 1,
    borderColor: colors.primary,
  },
  compareButton: {
    backgroundColor: colors.primary,
  },
  buttonText: {
    fontSize: 14,
    fontWeight: '600',
    textAlign: 'center',
    ...(Platform.OS === 'web' && {
      userSelect: 'none',
    }),
  },
  clearButtonText: {
    color: colors.primary,
  },
  compareButtonText: {
    color: colors.background.primary,
  },
}); 