import React, { useTransition } from 'react';
import { View, StyleSheet } from 'react-native';
import { NativeStackScreenProps } from '@react-navigation/native-stack';
import { RouteProp } from '@react-navigation/native';
import { RootStackParamList, ComparisonScreenNavigationProp } from '../types/navigation';
import ComparisonView from '../components/ComparisonView';
import { AppLayout } from '../components/AppLayout';
import { SEOHead } from '../components/SEOHead';
import { useFoils } from '../hooks/useFoils';
import { LoadingIndicator } from '../components/LoadingIndicator';
import { ErrorBoundary } from '../components/ErrorBoundary';
import { useApp } from '../context/AppContext';
import { getEnvironmentConfig } from '../config/environment';
import { trackPageView, trackAction, ANALYTICS_EVENTS } from '../utils/analytics';
import { useRoute, useNavigation } from '@react-navigation/native';
import { DEFAULT_COLUMNS } from '../constants/layout';
import { useQuivers } from '../hooks/useQuivers';
import { BaseProduct } from '../types/scraper';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { routes } from '../utils/routes';
import { FilterOptions } from '../types/scraper';

type ComparisonScreenParams = {
  foils?: BaseProduct[];
  quiverName?: string;
  searchQuery?: string;
};

interface ComparisonScreenProps {
  route: RouteProp<RootStackParamList, 'Comparison'>;
  navigation: ComparisonScreenNavigationProp;
}

const ComparisonScreen: React.FC<ComparisonScreenProps> = ({
  route,
  navigation
}) => {
  const [isPending, startTransition] = useTransition();
  const { params } = route;
  const { title, foils: initialFoils } = params || {};
  const { 
    searchQuery, 
    setSearchQuery, 
    selectedFoils: contextSelectedFoils,
    columns,
    setColumns,
    filters,
    setFilters
  } = useApp();
  const [foils, setFoils] = React.useState<BaseProduct[]>(initialFoils || contextSelectedFoils || []);
  const { foils: allFoils, loading: foilsLoading } = useFoils();
  const canonicalUrl = `${getEnvironmentConfig().webUrl}${routes.comparison}`;

  // If no foils are provided and we're not in a quiver view, redirect to browse
  React.useEffect(() => {
    if (!foilsLoading && !title && (!foils || foils.length === 0)) {
      startTransition(() => {
        navigation.replace('Browse', { mode: 'comparison' });
      });
      return;
    }
  }, [foils, foilsLoading, title, navigation]);

  // Show loading state while waiting for foils data or during transitions
  if (foilsLoading || !allFoils || isPending) {
    return (
      <AppLayout showFooter={true}>
        <LoadingIndicator />
      </AppLayout>
    );
  }

  // If we have no foils to compare and we're not loading, don't render anything
  // as we'll be redirected by the useEffect
  if (!foils || foils.length === 0) {
    return null;
  }

  // Define SEO props
  const seoProps = {
    title: title 
      ? `FoilQuiver - ${title} Comparison`
      : "FoilQuiver - Compare Hydrofoils",
    description: `Compare ${foils.length} hydrofoils side by side. Analyze specifications, prices, and features to find your perfect match.`,
    canonicalUrl
  };

  const handleClose = () => {
    startTransition(() => {
      navigation.goBack();
    });
  };

  const handleUpdateQuiver = (updatedFoils: BaseProduct[]) => {
    if (!updatedFoils || updatedFoils.length === 0) {
      startTransition(() => {
        navigation.replace('Browse', { mode: 'comparison' });
      });
      return;
    }
    
    setFoils(updatedFoils);
    if (title) {
      trackAction(ANALYTICS_EVENTS.UPDATE_QUIVER, {
        quiverName: title,
        foilCount: updatedFoils.length
      });
    }
  };

  const handleSearch = (query: string) => {
    setSearchQuery(query);
    trackAction(ANALYTICS_EVENTS.SEARCH_FOILS, {
      screen: 'comparison',
      query
    });
  };

  // Handle column visibility change
  const handleColumnVisibilityChange = (columnId: string) => {
    setColumns(prev => 
      prev.map(col => 
        col.id === columnId ? { ...col, isVisible: !col.isVisible } : col
      )
    );
  };

  // Handle filter changes
  const handleFilterChange = (type: keyof FilterOptions, value: string) => {
    setFilters(prev => ({
      ...prev,
      [type]: prev[type].includes(value)
        ? prev[type].filter(item => item !== value)
        : [...prev[type], value]
    }));
  };

  const handleClearFilters = () => {
    setFilters({ brands: [], series: [], type: [] });
  };

  // Create tableSettingsProps for AppHeader
  const tableSettingsProps = {
    filterOptions: {
      brands: Array.from(new Set(allFoils?.map(f => f.brand) || [])),
      series: Array.from(new Set(allFoils?.map(f => f.series).filter((s): s is string => !!s) || [])),
      types: Array.from(new Set(allFoils?.map(f => f.type).filter((t): t is 'front' | 'tail' => t === 'front' || t === 'tail') || []))
    },
    selectedBrands: filters.brands,
    selectedSeries: filters.series,
    selectedTypes: filters.type,
    onFilterChange: handleFilterChange,
    onClearFilters: handleClearFilters,
    columns,
    onColumnVisibilityChange: handleColumnVisibilityChange,
    onResetColumns: () => setColumns(DEFAULT_COLUMNS),
    foils: allFoils || [],
  };

  return (
    <AppLayout showFooter={true}>
      <SEOHead {...seoProps} />
      <View style={styles.tableContainer}>
        <ComparisonView
          foils={foils}
          allFoils={allFoils}
          columns={columns}
          onClose={handleClose}
          quiverName={title}
          onUpdateQuiver={handleUpdateQuiver}
          searchQuery={searchQuery}
          onSearch={handleSearch}
          onColumnVisibilityChange={handleColumnVisibilityChange}
          onResetColumns={() => setColumns(DEFAULT_COLUMNS)}
          filterOptions={{
            brands: Array.from(new Set(allFoils?.map(f => f.brand) || [])),
            series: Array.from(new Set(allFoils?.map(f => f.series).filter((s): s is string => !!s) || [])),
            types: Array.from(new Set(allFoils?.map(f => f.type).filter((t): t is 'front' | 'tail' => t === 'front' || t === 'tail') || []))
          }}
          selectedFilters={filters}
          onFilterChange={handleFilterChange}
          onClearFilters={handleClearFilters}
        />
      </View>
    </AppLayout>
  );
};

const styles = StyleSheet.create({
  tableContainer: {
    flex: 1,
    flexDirection: 'column',
    width: '100%',
    position: 'relative',
  },
});

export default ComparisonScreen; 