import React from 'react';
import { View, Text, Pressable, StyleSheet, Platform, useWindowDimensions } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { RootStackNavigationProp } from '../types/navigation';
import { useTheme } from '../theme/ThemeContext';
import { spacing } from '../theme/spacing';

export default function Footer() {
  const navigation = useNavigation<RootStackNavigationProp>();
  const { width } = useWindowDimensions();
  const { colors } = useTheme();
  const isMobile = width < 768;

  return (
    <View style={[
      styles.container, 
      isMobile && styles.containerMobile,
      { 
        backgroundColor: colors.background.primary,
        borderTopColor: colors.border
      }
    ]}>
      <View style={[styles.content, isMobile && styles.contentMobile]}>
        <View style={[styles.linksContainer, isMobile && styles.linksContainerMobile]}>
          <View style={[styles.linkGroup, isMobile && styles.linkGroupMobile]}>
            <Pressable onPress={() => navigation.navigate('Terms')}>
              <Text style={[styles.link, { color: colors.text.secondary }]}>Terms of Service</Text>
            </Pressable>
            <Text style={[styles.separator, { color: colors.text.tertiary }]}>•</Text>
            <Pressable onPress={() => navigation.navigate('Privacy')}>
              <Text style={[styles.link, { color: colors.text.secondary }]}>Privacy Policy</Text>
            </Pressable>
            <Text style={[styles.separator, { color: colors.text.tertiary }]}>•</Text>
            <Pressable onPress={() => navigation.navigate('Contact')}>
              <Text style={[styles.link, { color: colors.text.secondary }]}>Contact Us</Text>
            </Pressable>
          </View>
        </View>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    width: '100%',
    padding: spacing.LARGE,
  },
  containerMobile: {
    padding: 0,
  },
  content: {
    padding: 0,
    borderTopWidth: 1,
    maxWidth: 1200,
    marginHorizontal: 'auto',
  },
  contentMobile: {
    flexDirection: 'column',
    gap: 8,
  },
  linksContainer: {
    width: '100%',
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
  },
  linksContainerMobile: {
    flexDirection: 'column',
    gap: spacing.SMALL,
  },
  linkGroup: {
    flexDirection: 'row',
    alignItems: 'center',
    gap: spacing.LARGE,
  },
  linkGroupMobile: {
    gap: 8,
  },
  link: {
    fontSize: 14,
  },
  separator: {
    fontSize: 14,
  },
}); 